@font-face {
  font-family: "Neuzeit Grotesk";
  src: url("../fonts/NeuzeitGrotesk-Regular.woff") format("woff");
  font-weight: normal; }

@font-face {
  font-family: "Neuzeit Grotesk";
  src: url("../fonts/NeuzeitGrotesk-Bold.woff") format("woff");
  font-weight: bold; }

html,
body {
  width: 100%;
  height: 100%; }

body {
  font-family: "Neuzeit Grotesk", sans-serif;
  font-size: 16px;
  line-height: 1.8;
  color: #b4c2d8;
  background: #1d2024;
  -webkit-font-smoothing: antialiased; }

h1 {
  font-weight: bold;
  font-size: 1.8em;
  margin: 0;
  color: #fff; }

p {
  margin-bottom: 1em; }

a {
  color: #fff;
  opacity: 0.5;
  -webkit-transition: opacity 0.3s ease-out;
  -o-transition: opacity 0.3s ease-out;
  transition: opacity 0.3s ease-out; }
  a:hover, a:active {
    opacity: 1; }



